import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { Projeto } from '../interfaces/projeto';

@Injectable({
  providedIn: 'root'
})
export class ApiProjetos {

  url = `${environment.API}projetos/`;

  constructor(private httpClient: HttpClient) { }

  httpOptions = {
    headers: ({'Authorization':'Bearer '+sessionStorage.getItem('access'),'Content-Type': 'application/json'})
  }

  refreshToken(){
    const data = sessionStorage.getItem('refresh');
    
    fetch(`${environment.API}token/refresh/`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        refresh: data
      })
    })
    .then((response) => response.json())
    
    .then((data) => {
      const token=(JSON.stringify(data));
      const parse = JSON.parse(token);
      const access = parse.access;
      sessionStorage.setItem('access',access)
      console.log('API JWT TOKEN: Token atualizado com sucesso!');
    })
    
    .catch((error) => {
      console.error('Erro:', error);
    });
    
    }

  get(): Observable<Projeto[]> {
    return this.httpClient.get<Projeto[]>(this.url, this.httpOptions)
      .pipe(
        retry(2),
        catchError(this.handleError))
  }


  // Obtem um usuario pelo id
  getById(id: number): Observable<Projeto> {
    return this.httpClient.get<Projeto>(this.url + id, this.httpOptions)
      .pipe(
        retry(2),
        catchError(this.handleError)
      )
  }

  // salva um usuario
  save(obj: Projeto): Observable<Projeto> {
    return this.httpClient.post<Projeto>(this.url, JSON.stringify(obj), this.httpOptions)
      .pipe(
        retry(2),
        catchError(this.handleError)
      )
  }
  
  // utualiza um usuario
  update(obj: Projeto): Observable<Projeto> {
    return this.httpClient.put<Projeto>(this.url + obj.id, JSON.stringify(obj), this.httpOptions)
      .pipe(
        retry(1),
        catchError(this.handleError)
      )
  }
  // deleta um usuario
  delete(obj: Projeto) {
    return this.httpClient.delete<Projeto>(this.url + obj.id, this.httpOptions)
      .pipe(
        retry(1),
        catchError(this.handleError)
      )
  }

  // Manipulao de erros
  handleError(error: HttpErrorResponse) {
    let errorMessage = '';
    if (error.error instanceof ErrorEvent) {
      // Erro ocorreu no lado do client
      errorMessage = error.error.message;
    } else {
      // Erro ocorreu no lado do servidor
      errorMessage = `Cdigo do erro: ${error.status}, ` + `menssagem: ${error.message}`;
      if (error.status === 401){
        this.refreshToken()
      }

    }
    console.log(errorMessage);
    return throwError(errorMessage);
  };
}

