import { AuthenticationResult } from '@azure/msal-browser';
import { MsalService } from '@azure/msal-angular';
import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { ApiRequestMsg } from 'src/app/services/api/api-request-msg.service';
import { Mensagem } from 'src/app/models/mensagem'
import { Providers } from '@microsoft/mgt';



export enum ProviderState {
  Loading,
  SignedOut,
  SignedIn
}



@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})


export class HeaderComponent implements OnInit 

{
  objeto = {} as Mensagem;
  objetos: Mensagem[] = [];

  constructor(private authService: MsalService, public dialog: MatDialog, private api: ApiRequestMsg) {

  }
  ngOnInit(): void {
    this.authService.instance.handleRedirectPromise().then( res => {
      if (res != null && res.account != null) {
        this.authService.instance.setActiveAccount(res.account)
      }
    })
  }

  get(){
    this.api.get().subscribe((objetos: Mensagem[])=>{
      this.objetos = objetos;
    });
  }

  isLoggedIn(): boolean {
    return this.authService.instance.getActiveAccount() != null
  }

  

  login() {
    // this.authService.loginRedirect();
    this.authService.loginPopup()
      .subscribe((response: AuthenticationResult) => {
        this.authService.instance.setActiveAccount(response.account);
        Providers.globalProvider.setState(ProviderState.SignedIn);
      });
  }

  logout() {
    this.authService.logout()
  }

  getName() : any {
    if (this.authService.instance.getActiveAccount() == null) {
    }
    return this.authService.instance.getActiveAccount()?.name
  }


  getStatus() : any {
    if (this.authService.instance.getActiveAccount() == null){
      return 'login'
    }
    return 'logout'
  }


  getEmail() : any {
    return this.authService.instance.getActiveAccount()?.username
  }


  buttonLogin() {
    if (this.authService.instance.getActiveAccount() == null){
      this.login()
    }
    if (this.authService.instance.getActiveAccount() != null){
      this.logout()
    }
  }

}

