import { Component, OnChanges, OnInit } from '@angular/core';
import { Consultor } from 'src/app/views/rh/consultores/interfaces/consultores'
import { NgForm } from '@angular/forms';
import {MatDialog} from '@angular/material/dialog';
import { ApiConsultores } from './services/api-consultores.service';

@Component({
  selector: 'app-consultores',
  templateUrl: './consultores.component.html',
  styleUrls: ['./consultores.component.css']
})
export class ConsultoresComponent implements OnInit {

  user = {} as Consultor;
  users: Consultor[] = [];

  constructor(private apiConsultores: ApiConsultores, public dialog: MatDialog) { }

  ngOnInit(): any {
    this.getUsers();
  }
  
  //define se o usuario ser criado ou atualizado
  saveUser(form: NgForm){
    if (this.user.id !== undefined){
      this.apiConsultores.updateUser(this.user).subscribe(()=>{
        console.log("Usurio atualizado!")
        this.cleanForm(form);
      });
    } else {
      this.apiConsultores.saveUser(this.user).subscribe(()=>{
        console.log("Usurio cadastrado!")
        this.cleanForm(form);
      });
    }
  }


  getCNH(){
    window.location.href = 'www.google.com';
    this.apiConsultores.getUsers().subscribe((users: Consultor[])=>{
    })
  }


  //chama-se o servio para obter todos os usuarios
  getUsers(){
    this.apiConsultores.getUsers().subscribe((users: Consultor[])=>{
      this.users = users;
    });
  }

  //deleta um usuario
  deleteUser(user: Consultor){
    this.apiConsultores.deleteUser(user).subscribe(()=>{
      this.getUsers();
    });
  }

  //copia um usuario para ser editado
  editUser(user: Consultor){
    this.user = { ...user };
  }

  //limpa o formulario
  cleanForm(form: NgForm){
    this.getUsers();
    form.resetForm();
  }

}

