import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Consultor } from '../../interfaces/consultores'
import { ApiConsultores } from '../../services/api-consultores.service';




@Component({
  selector: 'app-cadastro-consultor',
  templateUrl: './cadastro-consultor.component.html',
  styleUrls: ['./cadastro-consultor.component.css']
})



export class CadastroConsultorComponent implements OnInit {

  user = {} as Consultor;
  users: Consultor[] = [];

  constructor(private apiConsultores: ApiConsultores) { }

  ngOnInit(): any {
    this.getUsers();
  }
  
  //define se o usuario ser criado ou atualizado
  saveUser(form: NgForm){
    if (this.user.id !== undefined){
      this.apiConsultores.updateUser(this.user).subscribe(()=>{
        this.cleanForm(form);
        alert('Consultor atualizado')
        window.history.back()
      });
    } else {
      this.apiConsultores.saveUser(this.user).subscribe(()=>{
        this.cleanForm(form);
        alert('Consultor cadastrado')
        window.history.back()
      });
    }
  }

  //chama-se o servio para obter todos os usuarios
  getUsers(){
    this.apiConsultores.getUsers().subscribe((users: Consultor[])=>{
      this.users = users;
    });
  }

  //deleta um usuario
  deleteUser(user: Consultor){
    this.apiConsultores.deleteUser(user).subscribe(()=>{
      alert('Consultor deletado')
      this.getUsers();
    });
  }

  //copia um usuario para ser editado
  editUser(user: Consultor){
    this.user = { ...user };
  }

  //limpa o formulario
  cleanForm(form: NgForm){
    this.getUsers();
    form.resetForm();
  }

}