import { Component, OnInit } from '@angular/core';
import { ApiClientes } from '../../services/api-clientes.service';
import { NgForm } from '@angular/forms';
import { Cliente } from '../../interfaces/cliente';


@Component({
  selector: 'app-cadastro-clientes',
  templateUrl: './cadastro-clientes.component.html',
  styleUrls: ['./cadastro-clientes.component.css']
})
export class CadastroClientesComponent implements OnInit {

  cliente = {} as Cliente;
  clientes: Cliente[] = [];

  constructor(private apiClientes: ApiClientes) { }

  ngOnInit(): void {
    this.get();
  }

  //define se o usuario ser criado ou atualizado
  save(form: NgForm){
    if (this.cliente.id !== undefined){
      this.apiClientes.update(this.cliente).subscribe(()=>{
        alert("Cliente atualizado!")
        this.cleanForm(form);
        window.history.back()
      });
    } else {
      this.apiClientes.save(this.cliente).subscribe(()=>{
        alert("Cliente cadastrado!")
        this.cleanForm(form);
        window.history.back()
      });
    }
  }


  //chama-se o servio para obter todos os usuarios
  get(){
    this.apiClientes.get().subscribe((clientes: Cliente[])=>{
      this.clientes = clientes;
    });
  }

  //deleta um usuario
  delete(cliente: Cliente){
    this.apiClientes.delete(cliente).subscribe(()=>{
      this.get();
    });
  }

  //copia um usuario para ser editado
  edit(cliente: Cliente){
    this.cliente = { ...cliente };
  }

  //limpa o formulario
  cleanForm(form: NgForm){
    this.get();
    form.resetForm();
  }

}

