import { Component, OnInit } from '@angular/core';
import { Usuario } from 'src/app/views/rh/colaboradores/interfaces/usuario'
import { environment } from 'src/environments/environment';
import { ApiColaboradores } from '../rh/colaboradores/services/api-colaboradores.service';



// Obtm a data/hora atual
var data = new Date();

// Guarda cada pedao em uma varivel
var dia     = data.getDate();           // 1-31
var dia_sem = data.getDay();            // 0-6 (zero=domingo)
var mes     = data.getMonth();          // 0-11 (zero=janeiro)          // 2 dgitos
var ano4    = data.getFullYear();       // 4 dgitos
var hora    = data.getHours();          // 0-23
var min     = data.getMinutes();        // 0-59
var seg     = data.getSeconds();        // 0-59
var mseg    = data.getMilliseconds();   // 0-999
var tz      = data.getTimezoneOffset(); // em minutos

// Formata a data e a hora (note o ms + 1)
var str_data = dia + '/' + ('0')+(mes+1) + '/' + ano4;


// Formata o dia da semana
var dias = new Array(
  'domingo','segunda','tera','quarta','quinta','sexta','sbado'
 );

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  user = {} as Usuario;
  users: Usuario[] = [];
  

  constructor(private apiColaboradores: ApiColaboradores) { }
  
  ngOnInit(): void {
    this.getUsers()
  }

  refreshToken(){
    const data = sessionStorage.getItem('refresh');
    
    fetch(`${environment.API}token/refresh/`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        refresh: data
      })
    })
    .then((response) => response.json())
    
    .then((data) => {
      const token=(JSON.stringify(data));
      const parse = JSON.parse(token);
      const access = parse.access;
      sessionStorage.setItem('access',access)
      console.log('API JWT TOKEN: Token atualizado com sucesso!');
    })
    
    .catch((error) => {
      console.error('Erro:', error);
    });
    
    }

  getUsers(){
    this.apiColaboradores.getUsers().subscribe((users: Usuario[])=>{
      this.users = users;
    });
  }

  data(){
  return 'Hoje  ' + dias[data.getDay()]+', ' + str_data}

}




