import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

//importa ts de funo de log MSAL.
import { MicrosoftLoginGuard } from './microsoft-login.guard';

//importa paginas principais caracterizadas por contedo publico e restrito.
import { PublicoComponent } from './ambientes/publico/publico.component';
import { RestritoComponent } from './ambientes/restrito/restrito.component';

//importa as pgins referentes aos views, para visualizao dentro do contedo restrito.
import { DashboardComponent } from './views/dashboard/dashboard.component';
import { RhComponent } from './views/rh/rh.component';
import { ComercialComponent } from './views/comercial/comercial.component';
import { ProjetosComponent } from './views/projetos/projetos.component';
import { GestaoComponent } from './views/gestao/gestao.component';
import { InfraestruturaComponent } from './views/infraestrutura/infraestrutura.component';
import { MarketingComponent } from './views/marketing/marketing.component';
import { BugReportComponent } from './views/bug-report/bug-report.component';

//importa componentes internos da aba RH
import { RecibosComponent } from './views/rh/consultores/app/recibos/recibos.component';
import { AgendaComponent } from './views/rh/consultores/app/agenda/agenda.component';
import { HoleritesComponent } from './views/rh/colaboradores/app/holerites/holerites.component';
import { CadastroConsultorComponent } from './views/rh/consultores/app/cadastro-consultor/cadastro-consultor.component';
import { CadastroColaboradorComponent } from './views/rh/colaboradores/app/cadastro-colaborador/cadastro-colaborador.component';
import { CadastroProjetosComponent } from './views/projetos/app/cadastro-projetos/cadastro-projetos.component';
import { CadastroClientesComponent } from './views/comercial/app/cadastro-clientes/cadastro-clientes.component';


const routes: Routes = [
  //bloco referente a autenticao inicial de acesso ao ERP.
  {path: '', component: PublicoComponent},
  {path: 'auth', component: PublicoComponent},
  {path: 'painel', component: RestritoComponent, canActivate: [MicrosoftLoginGuard]},

  //bloco referente ao contedo das views para ser exebido no painel principal
  {path: 'dashboard', component: DashboardComponent, canActivate: [MicrosoftLoginGuard]},
  {path: 'rh', component: RhComponent, canActivate: [MicrosoftLoginGuard]},
  {path: 'comercial', component: ComercialComponent,canActivate: [MicrosoftLoginGuard]},
  {path: 'projetos', component: ProjetosComponent,canActivate: [MicrosoftLoginGuard]},
  {path: 'gestao', component: GestaoComponent,canActivate: [MicrosoftLoginGuard]},
  {path: 'infraestrutura', component: InfraestruturaComponent,canActivate: [MicrosoftLoginGuard]},
  {path: 'marketing', component: MarketingComponent, canActivate: [MicrosoftLoginGuard]},
  {path: 'bug_report', component: BugReportComponent, canActivate: [MicrosoftLoginGuard]},

  //bloco referente a importao de funcionalidades internas das views
  {path: 'recibos', component: RecibosComponent, canActivate: [MicrosoftLoginGuard]},
  {path: 'agenda', component: AgendaComponent, canActivate: [MicrosoftLoginGuard]},
  {path: 'holerites', component: HoleritesComponent, canActivate: [MicrosoftLoginGuard]},
  {path: 'cadastro_consultor', component: CadastroConsultorComponent, canActivate: [MicrosoftLoginGuard]},
  {path: 'cadastro_colaborador', component: CadastroColaboradorComponent, canActivate: [MicrosoftLoginGuard]},
  {path: 'cadastro_projeto', component: CadastroProjetosComponent, canActivate: [MicrosoftLoginGuard]},
  {path: 'cadastro_cliente', component: CadastroClientesComponent, canActivate: [MicrosoftLoginGuard]},
  
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
