import { Usuario } from '../interfaces/usuario';
import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';
import { environment } from 'src/environments/environment'

@Injectable({
  providedIn: 'root'
})
export class ApiColaboradores {

  url = `${environment.API}cltistas/`;

  // injetando o HttpClient
  constructor(private httpClient: HttpClient) { }

  // Headers
  httpOptions = {

    headers:({'Authorization':'Bearer '+sessionStorage.getItem('access'),'Content-Type': 'application/json'})
  }

  refreshToken(){
    const data = sessionStorage.getItem('refresh');
    
    fetch(`${environment.API}token/refresh/`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        refresh: data
      })
    })
    .then((response) => response.json())
    
    .then((data) => {
      const token=(JSON.stringify(data));
      const parse = JSON.parse(token);
      const access = parse.access;
      sessionStorage.setItem('access',access)
      console.log('API JWT TOKEN: Token atualizado com sucesso!');
    })
    
    .catch((error) => {
      console.error('Erro:', error);
    });
    
    }

  // Obtem todos os usuarios
  getUsers(): Observable<Usuario[]> {
    return this.httpClient.get<Usuario[]>(this.url, this.httpOptions)
      .pipe(
        retry(2),
        catchError(this.handleError))
  }

  // Obtem um usuario pelo id
  getUserById(id: number): Observable<Usuario> {
    return this.httpClient.get<Usuario>(this.url + id, this.httpOptions)
      .pipe(
        retry(2),
        catchError(this.handleError)
      )
  }

  // salva um usuario
  saveUser(user: Usuario): Observable<Usuario> {
    return this.httpClient.post<Usuario>(this.url, JSON.stringify(user), this.httpOptions)
      .pipe(
        retry(2),
        catchError(this.handleError)
      )
  }
  
  // utualiza um usuario
  updateUser(user: Usuario): Observable<Usuario> {
    return this.httpClient.put<Usuario>(this.url + user.id, JSON.stringify(user), this.httpOptions)
      .pipe(
        retry(1),
        catchError(this.handleError)
      )
  }

  // deleta um usuario
  deleteUser(user: Usuario) {
    return this.httpClient.delete<Usuario>(this.url + user.id, this.httpOptions)
      .pipe(
        retry(1),
        catchError(this.handleError)
      )
  }

  // Manipulao de erros
  handleError(error: HttpErrorResponse) {
    let errorMessage = '';
    if (error.error instanceof ErrorEvent) {
      // Erro ocorreu no lado do client
      errorMessage = error.error.message;
    } else {
      // Erro ocorreu no lado do servidor
      errorMessage = `Cdigo do erro: ${error.status}, ` + `menssagem: ${error.message}`;
      if (error.status === 401){
        this.refreshToken()
      }

    }
    console.log(errorMessage);
    return throwError(errorMessage);
  };

}