import { ApiColaboradores } from './services/api-colaboradores.service';
import { Component, OnChanges, OnInit } from '@angular/core';
import { Usuario } from 'src/app/views/rh/colaboradores/interfaces/usuario'
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-colaboradores',
  templateUrl: './colaboradores.component.html',
  styleUrls: ['./colaboradores.component.css']
})

export class ColaboradoresComponent implements OnInit {
  
  user = {} as Usuario;
  users: Usuario[] = [];

  constructor(private apiColaboradores: ApiColaboradores ) { }

  ngOnInit(): any {
    this.getUsers();
  }
  
  //define se o usuario ser criado ou atualizado
  saveUser(form: NgForm){
    if (this.user.id !== undefined){
      this.apiColaboradores.updateUser(this.user).subscribe(()=>{
        console.log("Usurio atualizado!")
        this.cleanForm(form);
      });
    } else {
      this.apiColaboradores.saveUser(this.user).subscribe(()=>{
        console.log("Usurio cadastrado!")
        this.cleanForm(form);
      });
    }
  }


  //chama-se o servio para obter todos os usuarios
  getUsers(){
    this.apiColaboradores.getUsers().subscribe((users: Usuario[])=>{
      this.users = users;
    });
  }

  //deleta um usuario
  deleteUser(user: Usuario){
    this.apiColaboradores.deleteUser(user).subscribe(()=>{
      this.getUsers();
    });
  }

  //copia um usuario para ser editado
  editUser(user: Usuario){
    this.user = { ...user };
  }

  //limpa o formulario
  cleanForm(form: NgForm){
    this.getUsers();
    form.resetForm();
  }

}
