import { Mensagem } from './../../models/mensagem';
import { Injectable } from '@angular/core';
import { HttpClient, HttpErrorResponse, HttpHeaders } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { retry, catchError } from 'rxjs/operators';

import { environment } from 'src/environments/environment'


@Injectable({
  providedIn: 'root'
})
export class ApiRequestMsg {

  objeto = 'msg';
  url = `${environment.API}comunicacao/`;

  // injetando o HttpClient
  constructor(private httpClient: HttpClient) { }

  // Headers
  httpOptions = {
    headers:({'Authorization':'Bearer '+sessionStorage.getItem('access'),'Content-Type': 'application/json'})
  }

  refreshToken(){
    const data = sessionStorage.getItem('refresh');
    
    fetch(`${environment.API}token/refresh/`, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        refresh: data
      })
    })
    .then((response) => response.json())
    
    .then((data) => {
      const token=(JSON.stringify(data));
      const parse = JSON.parse(token);
      const access = parse.access;
      sessionStorage.setItem('access',access)
      console.log('API JWT TOKEN: Token atualizado com sucesso!');
    })
    
    .catch((error) => {
      console.error('Erro:', error);
    });
    
    }

  // Obtem todos os objetos
  get(): Observable<Mensagem[]> {
    return this.httpClient.get<Mensagem[]>(this.url, this.httpOptions)
      .pipe(
        retry(0),
        catchError(this.handleError))
  }

  // Obtem um objeto pelo id
  getId(id: number): Observable<Mensagem> {
    return this.httpClient.get<Mensagem>(this.url + '/' + id, this.httpOptions)
      .pipe(
        retry(2),
        catchError(this.handleError)
      )
  }

  // salva um objeto
  save(objeto: Mensagem): Observable<Mensagem> {
    return this.httpClient.post<Mensagem>(this.url, JSON.stringify(objeto), this.httpOptions)
      .pipe(
        retry(2),
        catchError(this.handleError)
      )
  }
  // utualiza um objeto
  update(objeto: Mensagem): Observable<Mensagem> {
    return this.httpClient.put<Mensagem>(this.url + '/' + objeto.id, JSON.stringify(objeto), this.httpOptions)
      .pipe(
        retry(1),
        catchError(this.handleError)
      )
  }

  // deleta um objeto
  delete(objeto: Mensagem) {
    return this.httpClient.delete<Mensagem>(this.url + '/' + objeto.id, this.httpOptions)
      .pipe(
        retry(1),
        catchError(this.handleError)
      )
  }

  // Manipulao de erros
  handleError(error: HttpErrorResponse) {
    let errorMessage = '';
    if (error.error instanceof ErrorEvent) {
      // Erro ocorreu no lado do client
      errorMessage = error.error.message;
    } else {
      // Erro ocorreu no lado do servidor
      errorMessage = `Cdigo do erro: ${error.status}, ` + `menssagem: ${error.message}`;
      if (error.status === 401){
        this.refreshToken()
      }
    }
    console.log(errorMessage);
    return throwError(errorMessage);
  };

}
