import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Holerite } from '../../interfaces/holerite';
import { Demonstrativo } from '../../interfaces/demonstrativo';
import { ApiHolerites } from '../../services/api-holerites.service'
import { ApiColaboradores } from '../../services/api-colaboradores.service';
import { ApiIrpf } from '../../services/api-irpf.service';
import { Usuario } from '../../interfaces/usuario';


@Component({
  selector: 'app-holerites',
  templateUrl: './holerites.component.html',
  styleUrls: ['./holerites.component.css']
})
export class HoleritesComponent implements OnInit {

  user = {} as Usuario;
  users: Usuario[] = [];
  holerite = {} as Holerite;
  holerites: Holerite[] = [];
  demonstrativo = {} as Demonstrativo;
  demonstrativos: Demonstrativo[]=[];


  constructor(private apiHolerites: ApiHolerites, private apiColaboradores: ApiColaboradores, private apiIrpf: ApiIrpf) { }

  ngOnInit(): void {
    this.get();
    this.getIrpf();
  }

  //define se o usuario ser criado ou atualizado
  save(form: NgForm){
    if (this.holerite.id !== undefined){
      this.apiHolerites.update(this.holerite).subscribe(()=>{
        console.log("Holerite atualizado!")
        this.cleanForm(form);
        window.history.back()
      });
    } else {
      this.apiHolerites.save(this.holerite).subscribe(()=>{
        console.log("Holerite cadastrado!")
        this.cleanForm(form);
        window.history.back()
      });
    }
  }


  //chama-se o servio para obter todos os usuarios
  get(){
    this.apiHolerites.get().subscribe((holerites: Holerite[])=>{
      this.holerites = holerites;
    });
  }

  getUsers(){
    this.apiColaboradores.getUsers().subscribe((users: Usuario[])=>{
      this.users = users;
    });
  }

  getIrpf(){
    this.apiIrpf.get().subscribe((demonstrativos: Demonstrativo[])=>{
      this.demonstrativos = demonstrativos;
    });
  }


  //deleta um usuario
  delete(holerite: Holerite){
    this.apiHolerites.delete(holerite).subscribe(()=>{
      this.get();
    });
  }

  //copia um usuario para ser editado
  edit(holerite: Holerite){
    this.holerite = { ...holerite };
  }

  //limpa o formulario
  cleanForm(form: NgForm){
    this.get();
    form.resetForm();
  }

}





