import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Projeto } from '../../interfaces/projeto';
import { ApiProjetos } from '../../services/api-projetos.service';

@Component({
  selector: 'app-cadastro-projetos',
  templateUrl: './cadastro-projetos.component.html',
  styleUrls: ['./cadastro-projetos.component.css']
})
export class CadastroProjetosComponent implements OnInit {


  projeto = {} as Projeto;
  projetos: Projeto[] = [];

  constructor(private apiProjetos: ApiProjetos) { }

  ngOnInit(): void {
    this.get();
  }

  //define se o usuario ser criado ou atualizado
  save(form: NgForm){
    if (this.projeto.id !== undefined){
      this.apiProjetos.update(this.projeto).subscribe(()=>{
        alert("Projeto atualizado!")
        this.cleanForm(form);
        window.history.back()
      });
    } else {
      this.apiProjetos.save(this.projeto).subscribe(()=>{
        alert("Projeto cadastrado!")
        this.cleanForm(form);
        window.history.back()
      });
    }
  }


  //chama-se o servio para obter todos os usuarios
  get(){
    this.apiProjetos.get().subscribe((projetos: Projeto[])=>{
      this.projetos = projetos;
    });
  }

  //deleta um usuario
  delete(projeto: Projeto){
    this.apiProjetos.delete(projeto).subscribe(()=>{
      this.get();
    });
  }

  //copia um usuario para ser editado
  edit(projeto: Projeto){
    this.projeto = { ...projeto };
  }

  //limpa o formulario
  cleanForm(form: NgForm){
    this.get();
    form.resetForm();
  }

}

