import { AuthenticationResult } from '@azure/msal-browser';
import { MsalService } from '@azure/msal-angular';
import { Component, OnInit} from '@angular/core';
import { Providers, Msal2Provider, TemplateHelper, PromptType } from '@microsoft/mgt';
import { LoginType } from '@microsoft/mgt-element';
import { Usuario } from 'src/app/views/rh/colaboradores/interfaces/usuario';
import { environment } from 'src/environments/environment';


const providerStateChanged = () => {
  if (Providers.globalProvider.state === ProviderState.SignedIn) {
  }
}

// register a callback for when the state changes
Providers.onProviderUpdated(providerStateChanged);

// remove callback if necessary
Providers.removeProviderUpdatedListener(providerStateChanged);

export enum ProviderState {
  Loading,
  SignedOut,
  SignedIn
}


@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})



export class AppComponent implements OnInit 
{
  user = {} as Usuario;
  users: Usuario[] = [];

  constructor(private authService: MsalService) {
  }
  ngOnInit(): void {
    this.getToken()
    this.graph(),
    this.authService.instance.handleRedirectPromise().then( res => {
      if (res != null && res.account != null) {
        this.authService.instance.setActiveAccount(res.account)
      }
    })
  }

  isLoggedIn(): boolean {
    if (Providers.globalProvider.state === ProviderState.SignedIn) {
      return this.authService.instance.getActiveAccount() != null
    }
    return this.authService.instance.getActiveAccount() != null
  }


  graph(){
    Providers.globalProvider = new Msal2Provider(
      {
      clientId: `${environment.MSAL_ID}`,
      authority: `${environment.MSAL_AUTHORITY}`,
      redirectUri: `${environment.URL}`,
        loginType: LoginType.Popup,
        prompt: PromptType.SELECT_ACCOUNT,
      }, 
    ),
    TemplateHelper.setBindingSyntax('[[',']]');
    
}

entrar(){
  Providers.globalProvider.setState(ProviderState.SignedIn);
  this.login()
}

login() {
  // this.authService.loginRedirect();
  this.authService.loginPopup()
    .subscribe((response: AuthenticationResult) => {
      this.authService.instance.setActiveAccount(response.account);
    });
}

getToken(){
const data = { username: `${environment.USER}`, password:`${environment.SENHA}` };

fetch(`${environment.API}token/`, {
  method: 'POST',
  headers: {
    'Content-Type': 'application/json',
  },
  body: JSON.stringify(data),
})
.then((response) => response.json())

.then((data) => {
  const token=(JSON.stringify(data));
  const parse = JSON.parse(token);
  const access = parse.access;
  const refresh = parse.refresh;
  sessionStorage.setItem('access',access)
  sessionStorage.setItem('refresh',refresh)
  console.log('API JWT TOKEN: Token obtido com sucesso!');
})

.catch((error) => {
  console.error('Erro:', error);
});

}

refreshToken(){
  const data = sessionStorage.getItem('refresh');
  
  fetch(`${environment.API}token/refresh/`, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify({
      refresh: data
    })
  })
  .then((response) => response.json())
  
  .then((data) => {
    const token=(JSON.stringify(data));
    const parse = JSON.parse(token);
    const access = parse.access;
    sessionStorage.setItem('access',access)
    console.log('API JWT TOKEN: Token atualizado com sucesso!');
  })
  
  .catch((error) => {
    console.error('Erro:', error);
  });
  
  }

}

