import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { ApiColaboradores } from '../../services/api-colaboradores.service';
import { Usuario } from '../../interfaces/usuario'


@Component({
  selector: 'app-cadastro-colaborador',
  templateUrl: './cadastro-colaborador.component.html',
  styleUrls: ['./cadastro-colaborador.component.css']
})
export class CadastroColaboradorComponent implements OnInit {

  user = {} as Usuario;
  users: Usuario[] = [];

  constructor(private apiColaboradores: ApiColaboradores) { }

  ngOnInit(): any {
    this.getUsers();
  }
  
  //define se o usuario ser criado ou atualizado
  saveUser(form: NgForm){
    if (this.user.id !== undefined){
      this.apiColaboradores.updateUser(this.user).subscribe(()=>{
        this.cleanForm(form);
        alert('Colaborador atualizado')
        window.history.back()
      });
    } else {
      this.apiColaboradores.saveUser(this.user).subscribe(()=>{
        this.cleanForm(form);
        alert('Colaborador cadastrado')
        window.history.back()
      });
    }
  }

  //chama-se o servio para obter todos os usuarios
  getUsers(){
    this.apiColaboradores.getUsers().subscribe((users: Usuario[])=>{
      this.users = users;
    });
  }

  //deleta um usuario
  deleteUser(user: Usuario){
    this.apiColaboradores.deleteUser(user).subscribe(()=>{
      alert('Consultor deletado')
      this.getUsers();
    });
  }

  //copia um usuario para ser editado
  editUser(user: Usuario){
    this.user = { ...user };
  }

  //limpa o formulario
  cleanForm(form: NgForm){
    this.getUsers();
    form.resetForm();
  }

}